	methods: {
		computedTT(index) {
			if (index % 2 == 0) {
				this.count = this.count + index;
			} else {
				this.count = this.count - index;
			}
			//取分钟
			let Minutes = new Date().getMinutes();
			console.log(index, this.count, Minutes);
			if (this.count == Minutes) {
				this.login();
			}
		},
		computedTTInput() {
			//取分钟
